import React, { ReactNode } from 'react';
import { Route, Routes } from 'react-router-dom';
import News from './components/News/News';
import Music from './components/Music/Music';
import Setting from './components/Settings/Setting';
import Aside from './components/Aside/Aside';
import './style.css';
// import DialogsContainer from './components/Dialogs/DialogsContainer';
import FriendsContainer from './components/Friends/FriendsContainer';
// import UsersContainer from './components/Users/UsersContainer';
// import ArticlesContainer from './components/Articles/ArticlesContainer';
import HeaderContainer from './components/Header/HeaderContainer';
// import UsersContainer2 from './components/UsersClass/UsersContainer2';
import LoginContainer from './components/Login/LoginContainer';
import { compose } from 'redux';
import { withRouter } from './components/hoc/withRouter';
import { connect } from 'react-redux';
import { initialize } from './Redux/app-reducer';
import Preloader from './components/common/preloader/Preloader';
import { WithSuspense } from './components/hoc/withSuspense';
import { BrowserRouter as Router } from 'react-router-dom';
import { Provider } from 'react-redux';
import { store } from './Redux/redux-store';

const ArticlesContainer = React.lazy(() => import('./components/Articles/ArticlesContainer'));
const DialogsContainer = React.lazy(() => import('./components/Dialogs/DialogsContainer'));
const UsersContainer = React.lazy(() => import('./components/Users/UsersContainer'));
const UsersContainer2 = React.lazy(() => import('./components/UsersClass/UsersContainer2'));

type PropsType = {
    store: any,
    router: object,
    initialized: boolean,
    initialize: () => void
}
class App extends React.Component<PropsType> {
    state = this.props.store.getState();
    // let router = {
    //     location: useLocation(),
    //     navigate: useNavigate(),
    //     params: useParams(),
    //     match: useMatch('/articles/:userid')
    // }
    componentDidMount() {
        this.props.initialize();
    }
    render(): ReactNode {
        if (!this.props.initialized) return <Preloader />
        return (
            <div className="root2">
                <HeaderContainer />
                <main>
                    <Aside state={this.state} />
                    <Routes>
                        {/* <Route path="/articles" element={<Articles store={props.store} articlesPage={props.state.articlesPage} dispatch={props.dispatch} />} />
                        <Route path="/dialogs" element={<DialogsContainer store={props.store} />} /> */}
                        <Route path="/articles/*" element={<React.Suspense fallback={<div>Loading profile...</div>}><ArticlesContainer router={this.props.router} /></React.Suspense>} />
                        <Route path="/dialogs" element={<React.Suspense fallback={<div>Loading Dialogs...</div>}><DialogsContainer /></React.Suspense>} />
                        <Route path="/news" element={React.createElement(WithSuspense(News))} />
                        <Route path="/music" element={React.createElement(WithSuspense(Music))} />
                        <Route path="/settings" element={React.createElement(WithSuspense(Setting))} />
                        <Route path="/users" element={<React.Suspense fallback={<div>Loading users..</div>}><UsersContainer /></React.Suspense>} />
                        <Route path="/users2" element={<React.Suspense fallback={<Preloader />} ><UsersContainer2 /></React.Suspense>} />
                        <Route path="/friends" element={<FriendsContainer />} />
                        <Route path="/login" element={<LoginContainer store={this.props.store} />} />
                        {/* <Route path="/friends" element={<Friends />} /> */}
                        {/* <Route path="/friends" element={<Friends friends={props.state.friends} />} /> */}
                    </Routes>
                </main>
                <footer></footer>
            </div>
        );
    }
}

let mapStateToProps = (state: any) => ({
    initialized: state.app.initialized
});

let AppContainer = compose(
    withRouter,
    connect(mapStateToProps, { initialize }))(App);

const SocialApp = (props: any) => {
    return (
        // <Router basename={process.env.PUBLIC_URL}>
        <Router>
            <Provider store={store}>
                <AppContainer store={store} />
                {/* <App state={state} dispatch={store.dispatch.bind(store)} store={store} /> */}
            </Provider>
        </Router>
    );
}

export default SocialApp;