import axios from "axios";

const instance = axios.create({
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    withCredentials: true,
    headers: {
        'API-KEY': '15325bdc-bc4f-4fed-b921-7b3eac784eff'
    }
});
const UsersAPI = {
    getUsersAPI(currentPage: number = 1, pageSize: number = 5) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`)
            .then(response => response.data);
    },
    async followUsersAPI(userid: number) {
        const response = await instance.post(`follow/${userid}`);
        return response.data;
    },
    async unFollowUsersAPI(userid: number) {
        const response = await instance.delete(`follow/${userid}`);
        return response.data;
    },
    async authMe() {
        const response = await instance.get(`auth/me`);
        return response.data;
    },
    async setUsersProfile(userid: number) {
        const response = await instance.get('profile/' + userid);
        return response.data;
    },
    async getProfileStatus(userid: number) {
        const response = await instance.get('profile/status/' + userid);
        return response.data;
    },
    async updateStatus(status: string) {
        const response = await instance.put('profile/status', {status});
        return response.data;
    },
    async Login(email: string, password: any, rememberMe: boolean = false) {
        const response = await instance.post('/auth/login', {email, password, rememberMe});
        return response.data;
    },
    async LogOut() {
        const resposne = await instance.delete('/auth/login');
        return resposne.data;
    },
    async savePhoto(file: any) {
        let formData = new FormData();
        formData.append('image', file);
        const response = await instance.put('/profile/photo', formData, {
            headers: {
                'Content-type': 'multipart/form-data'
            }
        });
        return response.data;
    }
}
export default UsersAPI;



