import { Users2Api } from "../components/UsersClass/api";

const TOTAL_USERS = 'TOTAL_USERS';
const CURRENT_PAGE = 'CURRENT_PAGE';
const CURRENT_USERS = 'CURRENT_USERS';
const IS_FETCHING = 'IS_FETCHING';
const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const FETCHING_PROGRESS = 'FETCHING_PROGRESS';

type initialStateType = {
    users: Array<object>,
    pageSize: number,
    totalUserCount: number,
    currentPage: number,
    isFetching: boolean,
    fetchingInProgress: Array<any>
}
let initialState: initialStateType = {
    users: [],
    pageSize: 5,
    totalUserCount: 0,
    currentPage: 1,
    isFetching: false,
    fetchingInProgress: []
}

const updateObjectInArray = (item: any, itemid: number, objPropsName: string, newObj: any) => {
    return item.map((i: any) => {
        if (i[objPropsName] === itemid) {
            return {...i, ...newObj};
        }
        return i;
    });
}

export const user2Reducer = (state: any = initialState, action: any) => {
    switch (action.type) {
        case TOTAL_USERS:
            return {
                ...state,
                totalUserCount: action.count
            }
        case CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.numberPage
            }
        case CURRENT_USERS:
            return {
                ...state,
                users: action.users
            }
        case IS_FETCHING:
            return {
                ...state,
                isFetching: action.status
            }
        case FOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userid, 'id', {followed: true})
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectInArray(state.users, action.userid, 'id', {followed: false})
            }
        case FETCHING_PROGRESS:
            return {
                ...state,
                fetchingInProgress: action.status
                ? [...state.fetchingInProgress, action.userid]
                : state.fetchingInProgress.filter((item: any) => item !== action.userid)
            }
        default:
            return state;
    }
}

const getTotalUserCount = (count: number) => ({type: TOTAL_USERS, count});
export const getCurrentPage = (numberPage: number) => ({type: CURRENT_PAGE, numberPage});
const getCurrentUsers = (users: any) => ({type: CURRENT_USERS, users});
const isFetching = (status: boolean) => ({type: IS_FETCHING, status});
const follow = (userid: number) => ({type: FOLLOW, userid});
const unFollow = (userid: number) => ({type: UNFOLLOW, userid});
const fetchingInProgressAction = (status: boolean, userid: number) => ({
    type: FETCHING_PROGRESS, status, userid
});

export const getCurrentUsersThunk = (pageSize: number, currentPage: number) => {
    return async (dispatch: any) => {
        dispatch(isFetching(true));
        const response = await Users2Api.getCurrentUsers(pageSize, currentPage);
        dispatch(isFetching(false));
        dispatch(getCurrentUsers(response.items));
        dispatch(getTotalUserCount(response.totalCount));
    }
}

const followUnfollowFlow = async (dispatch: any, userid: number, apiMethod: any, action: any) => {
    dispatch(fetchingInProgressAction(true, userid));
    const response = await apiMethod(userid);
    if (response.resultCode === 0) {
        dispatch(action(userid));
    }
    dispatch(fetchingInProgressAction(false, userid));
}

export const followThunk = (userid: number) => {
    return async (dispatch: any) => {
        followUnfollowFlow(dispatch, userid, Users2Api.follow.bind(Users2Api), follow);
    }
}
export const unFollowThunk = (userid: number) => {
    return async (dispatch: any) => {
        followUnfollowFlow(dispatch, userid, Users2Api.unFollow.bind(Users2Api), unFollow);
    }
}