const ADD_MESSAGE = "ADD-MESSAGE";
const ON_MESSAGE_CHANGE = "ON-MESSAGE-CHANGE";
type newMessageType = {
    id: number,
    message: string
};

let initialState = {
    dialogData: [
        { id: "1", name: "Igor" },
        { id: "2", name: "Sveta" },
        { id: "3", name: "Nada" },
        { id: "4", name: "Petr" },
        { id: "5", name: "Vladimir" }
    ],
    messageData: [
        { id: 1, message: "Hello" },
        { id: 2, message: "How are you?" },
        { id: 3, message: "Andrey YOU The Best" }
    ],
    newMessageText: "Enter You Text:"
};

export const messageReducer = (state: any = initialState, action: any): any => {
    switch (action.type) {
        case ADD_MESSAGE: {
            let newMessage: newMessageType = {
                id: Math.max.apply(null, state.messageData.map((item: any) => item.id)) + 1,
                message: action.message
            };
            return {
                ...state,
                messageData: [...state.messageData, newMessage],
                newMessageText: ""
            };
        }
        case ON_MESSAGE_CHANGE: {
            return {
                ...state,
                newMessageText: action.text
            };
        }
        default:
            return state;
    }
}
export const addMessageActionCreator = (message: string) => ({type: ADD_MESSAGE, message});
export const onMessageChangeActionCreator = (text: string) => ({type: ON_MESSAGE_CHANGE, text: text});
