import { authMeThunk } from "./auth-reducer";

const SET_INITIALIZED = "SET_INITIALIZED";

type initialStateType = {
    initialized: boolean
}
let initialState: initialStateType = {
    initialized: false
}

export const appReducer = (state: any = initialState, action: any) => {
    switch (action.type) {
        case SET_INITIALIZED:
            return {
                ...state,
                initialized: true
            }
        default:
            return state;
    }
}

const setInitialized = () => ({type: SET_INITIALIZED});
export const initialize = () => (dispatch: any) => {
    let promise = dispatch(authMeThunk());
    Promise.all([promise]).then(() => dispatch(setInitialized()));
}
