import React from 'react';
import { NavLink } from 'react-router-dom';
import classes from './Header.module.css';
const Header = (props: any) => {
    return (
        <header>
            <img className={classes.img} src="#" alt=''/>
            <p className={`${classes.p} ${classes.active}`}>Header</p>
            {
            !props.isAuth
            ? <div className={classes.loginBlock}><NavLink to="/login">Login</NavLink></div>
            : <div onClick={props.logOut} className={classes.loginBlock}>{props.login}</div>
            }
        </header>
    );
}

export default Header;