import UsersAPI from "../api/api";

const ADD_POST = "ADD-POST";
const ON_POST_CHANGE = "ON-POST-CHANGE";
const SET_USER_PROFILE = "SET_USER_PROFILE";
const SET_DEFAULT_USER_ID = "SET_DEFAULT_USER_ID";
const SET_STATUS = "SET_STATUS";
const DELETE_POST = "DELETE_POST";
const SAVE_PHOTO = "SAVE_PHOTO";

type newPostType = {
    id: number,
    messages: string,
    like: string
};

let initialState = {
    postData: [
        { id: 1, messages: "Hello, how are you?", like: "255" },
        { id: 2, messages: "Andrey You The Best", like: "540" },
        { id: 3, messages: "Learn Programming ReactJs", like: "150" }
    ],
    profile: null,
    userid: null,
    newPostText: "Enter Your Text:",
    status: "",
    urlPhoto: ""
};

export const articlesReducer = (state: any = initialState, action: any): any => {
    if (action.type === ADD_POST) {
        let newPost: newPostType = {
            id: Math.max.apply(null, state.postData.map((item: any) => item.id)) + 1,
            messages: action.messageBody,
            // messages: state.newPostText,
            like: "8"
        };
        return {
            ...state,
            postData: [...state.postData, newPost],
            newPostText: ""
        };
    } else if (action.type === ON_POST_CHANGE) {
        return {
            ...state,
            newPostText: action.text
        };
    } else if (action.type === SET_USER_PROFILE) {
        return {
            ...state,
            profile: action.profile
        }
    } else if (action.type === SET_DEFAULT_USER_ID) {
        return {
            ...state,
            userid: action.userid
        }
    } else if (action.type === SET_STATUS) {
        return {
            ...state,
            status: action.status
        }
    } else if (action.type === DELETE_POST) {
        return {
            ...state,
            postData: state.postData.filter((item: any) => item.id !== action.id)
        }
    } else if (action.type === SAVE_PHOTO) {
        return {
            ...state,
            profile: {...state.profile, photos: action.photos}
        }
    }
    return state;
}
export const addPostActionCreator = (messageBody: string) => ({ type: ADD_POST, messageBody });
export const onPostChangeActionCreator = (text: string) => ({ type: ON_POST_CHANGE, text: text });
const setUserProfile = (profile: object) => ({ type: SET_USER_PROFILE, profile });
const setDefaultUserId = (userid: number) => ({ type: SET_DEFAULT_USER_ID, userid });
const setStatusUserProfile = (status: string) => ({type: SET_STATUS, status});
export const deletePost = (id: number) => ({type: DELETE_POST, id});
const savePhotoAction = (photos: any) => ({type: SAVE_PHOTO, photos});

export const setUserProfileThunk = (userid: number) => (dispatch: any) => {
    UsersAPI.setUsersProfile(userid).then(response => dispatch(setUserProfile(response)));
}
export const setDefaultUseridThunk = () => {
    return (dispatch: any) => {
        UsersAPI.authMe().then(response => {
            if (response.resultCode === 0) {
                dispatch(setDefaultUserId(response.data.id));
                dispatch(setUserProfileThunk(response.data.id));
            }
        });
    }
}

export const getUserStatusThunk = (userid: number) => (dispatch: any) => {
    UsersAPI.getProfileStatus(userid).then(response => {
        dispatch(setStatusUserProfile(response));
    });
}

export const updateUserStatusThunk = (status: string) => {
    return (dispatch: any) => {
        UsersAPI.updateStatus(status).then(response => {
            if (response.resultCode === 0) dispatch(setStatusUserProfile(status));
        })
    }
}

export const savePhoto = (file: any) => async (dispatch: any) => {
    const response = await UsersAPI.savePhoto(file);
    console.log(response);
    if (response.resultCode === 0) {
        dispatch(savePhotoAction(response.data));
        dispatch(setDefaultUseridThunk());
        // <Navigate to="/articles" />
    }
}