import UsersAPI from "../api/api";

const FOLLOW = "FOLLOW";
const UNFOLLOW = "UNFOLLOW";
const SET_USERS = "SET_USERS";
const CHANGE_CURRENT_PAGE = "CHANGE_CURRENT_PAGE";
const SET_TOTAL_USERS = "SET_TOTAL_USERS";
const IS_FETCHING = "IS_FETCHING";
const FOLLOWING_PROGRESS = "FOLLOWING_PROGRESS";
type initialStateType = {
    persons: Array<object>,
    pageSize: number,
    totalUsersCount: number,
    currentPage: number,
    isFetching: boolean,
    followingInProgress: Array<object>
};

let initialState: initialStateType = {
    persons: [],
    pageSize: 5,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: []

};

const updateObjectInArray = (item: any, itemid: number, objProps: any, newObjProps: any) => {
    return item.map((n: any) => {
        if (n[objProps] === itemid) {
            return {...n, ...newObjProps};
        }
        return n;
    });
}

export const usersReducer = (state: any = initialState, action: any) => {
    switch (action.type) {
        case FOLLOW:
            return {
                ...state,
                persons: updateObjectInArray(state.persons, action.userid, 'id', {followed: true})
                // persons: state.persons.map((item: any) => {
                //     if (item.id === action.userid) {
                //         return { ...item, followed: true }
                //     }
                //     return item;
                // })
            };
        case UNFOLLOW:
            return {
                ...state,
                persons: updateObjectInArray(state.persons, action.userid, 'id', {followed: false})
                // persons: state.persons.map((item: any) => {
                //     if (item.id === action.userid) {
                //         return { ...item, followed: false }
                //     }
                //     return item;
                // })
            }
        case SET_USERS:
            return {
                ...state,
                persons: action.users
            }
        case CHANGE_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.currentPage
            }
        case SET_TOTAL_USERS:
            return {
                ...state,
                totalUsersCount: action.users
            }
        case IS_FETCHING:
            return {
                ...state,
                isFetching: action.status
            }
        case FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.status
                    ? [...state.followingInProgress, action.userid]
                    : state.followingInProgress.filter((id: number) => id !== action.userid)
            }
        default:
            return state;
    }
}

export const followActionCreator = (userid: number) => ({ type: FOLLOW, userid: userid });
export const unfollowActionCreator = (userid: number) => ({ type: UNFOLLOW, userid: userid });
export const setUsersActionCreator = (users: any) => ({ type: SET_USERS, users: users });
export const changeCurrentPageActionCreator = (currentPage: number) => ({ type: CHANGE_CURRENT_PAGE, currentPage: currentPage });
export const setTotalUsersCountActionCreator = (users: number) => ({ type: SET_TOTAL_USERS, users: users });
export const isFetchingActionCreator = (status: boolean) => ({ type: IS_FETCHING, status: status });
export const followingInProgressActionCreator = (status: boolean, userid: number) => ({ type: FOLLOWING_PROGRESS, status: status, userid: userid });

//shunk
export const getUsersThunk = (currentPage: number, pageSize: number) => {
    return (dispatch: any) => {
        dispatch(isFetchingActionCreator(true));
        UsersAPI.getUsersAPI(currentPage, pageSize)
            .then(response => {
                dispatch(isFetchingActionCreator(false));
                dispatch(setUsersActionCreator(response.items));
                dispatch(setTotalUsersCountActionCreator(response.totalCount));
            });
    }
}

const followUnfollowFlow = async (dispatch: any, userid: number, apiMethod: any, action: any) => {
        dispatch(followingInProgressActionCreator(true, userid));
        const response = await apiMethod(userid);
        if (response.resultCode === 0) dispatch(action(userid));
        dispatch(followingInProgressActionCreator(false, userid));
}

export const isUnFollowingThunk = (userid: number) => {
    return async (dispatch: any) => {
        followUnfollowFlow(dispatch, userid, UsersAPI.unFollowUsersAPI.bind(UsersAPI), unfollowActionCreator);
    }
}

export const isFollowingThunk = (userid: number) => {
    return async (dispatch: any) => {
        followUnfollowFlow(dispatch, userid, UsersAPI.followUsersAPI.bind(UsersAPI), followActionCreator);
    }
}





//  
export const getUsersThunkCreator = (currentPage: number, pageSize: number) => (dispatch: any) => {
    dispatch(isFetchingActionCreator(true));
    UsersAPI.getUsersAPI(currentPage, pageSize)
        .then(response => {
            dispatch(isFetchingActionCreator(false));
            dispatch(setUsersActionCreator(response.items));
            dispatch(setTotalUsersCountActionCreator(response.totalCount));
        });
}

