import React from 'react';
import { Navigate } from 'react-router-dom';
import { Field, reduxForm } from 'redux-form';
import { maxLength, requiredField } from '../../utils/validation/validation';
import { Input } from '../common/FormControls/FormControls';
import style from './../common/FormControls/FormControls.module.css';

const createField = (component: any, validate: any, name: string, placeholder: string, props: any) => (
    <div>
        <Field component={component} validate={validate} name={name} placeholder={placeholder} {...props} />
    </div>
);

const maxLength20 = maxLength(20);
let LoginForm = ({handleSubmit, error}: any) => {
    return (
        <form onSubmit={handleSubmit}>
            {createField(Input, [requiredField, maxLength20], 'email', 'login', {type: 'text'})}
            {createField(Input, [requiredField, maxLength20], 'password', 'password', {type: 'password'})}
            {
                error &&
                <div className={style.form__summery_error}><span>{error}</span></div>
            }
            <div><Field component={Input} type={"checkbox"} name={"rememberMe"} /> Remember Me</div>
            <div><button>Log In</button></div>
        </form>
    );
}
const ReduxLoginForm = reduxForm({form: 'login'})(LoginForm);
const Login = (props: any) => {
   if (props.isAuth) return <Navigate to='/articles' />
    const onSubmit = (formData: any) => {
        const {email, password, rememberMe} = formData;
        props.logIn(email, password, rememberMe);
    }
    return (
        <div>
            <p>LOGIN:</p>
            <ReduxLoginForm onSubmit={onSubmit} />
            {/* <LoginForm {...props} /> */}
        </div>
    );
}
// const onSubmit = (formData: any) => {
//     console.log(formData)
// }
// export default reduxForm({ form: 'login', onSubmit })(Login);
export default Login;